.video{
    position:relative;
    background-color:white;
    width:100%;
    height:100%;
    scroll-snap-align:start;
    z-index:0;
    

}
.video__player{
    object-fit:fill;
    width:100%;
    height:100%;

}
.heading{
    color:white;
    position:absolute;
    top:25px;
    right:25px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    z-index:1;
    font-weight:600;
    display:flex;
    direction:row;
    justify-content:center;
    align-items:center;


}
.icon{
    font-size:150px;
    color:red !important;
    background-color:red !important;
    
}
