.videoFooter{
    position: relative;
    color:white;
    bottom:150px;
    margin-left:40px;
    display: flex;
}
.videoFooter__text{
    flex:1;
}
.videoFooter__text >h3{
    padding-bottom:20px;
}
.videoFooter__text>p{
    padding-bottom:20px;
}
.videoFooter_icon{
    position:absolute;
}
.videoFooter_ticker >.ticker{
    height:fit-content;
    margin-left:30px;
    width:60%;
}
.videoFooter__record{
    animation: spinner infinite 5s linear;
    height:50px;
    
    position:absolute;
    bottom:0;
    border-radius: 50%;
    right:20px;
    
}
@keyframes spinner{
    from{
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg)
    }
}